<template>
  <div class="point-location">
    <div class="row-item">
      <el-input v-model.number="lng" />
      <span>-</span>
      <el-input v-model.number="lat" />
    </div>
    <div class="row-item">
      <el-button
        type="primary"
        @click="handleLocation"
      >
        
      </el-button>
    </div>
  </div>
</template>

<script>
import { coordTransInput } from '../../../../utils/GeometryUtil';

export default {
  name: 'PointLocation',
  props: {
    // WGS84()WGS84()BD09()
    inputCoordType: {
      type: String,
      default: 'GCJ02',
    },
  },
  data() {
    return {
      lng: '116.397451', // 
      lat: '39.909187', // 
    };
  },
  methods: {
    locationMarker(lng, lat) {
      const { amap, AMap } = window;

      amap.clearMap();

      const marker = new AMap.Marker({
        position: new AMap.LngLat(lng, lat),
      });

      amap.add(marker);
      amap.setFitView();
    },
    // 
    handleLocation() {
      const xy = coordTransInput(this.lng, this.lat, this.inputCoordType);
      this.locationMarker(xy[0], xy[1]);
    },
  },
};
</script>

<style lang="scss" scoped>
  .point-location {
    .row-item {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px 0;
    }
  }
</style>
