<template>
  <div class="extent-split">
    <div class="row-item">
      <span class="label"></span>
      <el-input v-model.number="minX" />
    </div>
    <div class="row-item">
      <span class="label"></span>
      <el-input v-model.number="minY" />
    </div>
    <div class="row-item">
      <span class="label"></span>
      <el-input v-model.number="maxX" />
    </div>
    <div class="row-item">
      <span class="label"></span>
      <el-input v-model.number="maxY" />
    </div>
    <div class="row-item">
      <span class="label">X</span>
      <el-input v-model.number="splitX" />
    </div>
    <div class="row-item">
      <span class="label">Y</span>
      <el-input v-model.number="splitY" />
    </div>
    <div class="row-item">
      <span class="label"></span>
      <el-input v-model.number="tolerance" />
    </div>
    <div class="row-item">
      <el-button
        type="primary"
        @click="handleLocation"
      >
        
      </el-button>
    </div>
  </div>
</template>

<script>
import randomColor from 'randomcolor';
import { extentSplit } from '../../../../utils/GeometryUtil';

export default {
  name: 'ExtentSplit',
  data() {
    return {
      minX: 116.3560295, // 
      minY: 39.90025506, // 
      maxX: 116.4344788, // 
      maxY: 39.94922702, // 
      splitX: 2, // X
      splitY: 2, // Y
      tolerance: 0.01, // 
    };
  },
  methods: {
    locationPolygons(paths) {
      const { amap, AMap } = window;
      amap.clearMap();

      paths.forEach((path) => {
        const color = randomColor({ luminosity: 'dark' });
        const polygon = new AMap.Polygon({
          path,
          fillColor: color, // 
          borderWeight: 2, //  1
          strokeColor: color, // 
        });

        amap.add(polygon);
        amap.setFitView();
      });
    },
    handleLocation() {
      const {
        minX, minY, maxX, maxY, splitX, splitY, tolerance,
      } = this;
      const paths = extentSplit({
        minX, minY, maxX, maxY,
      }, { splitX, splitY, tolerance });

      this.locationPolygons(paths);
    },
  },
};
</script>

<style lang="scss" scoped>
  .extent-split {
    .row-item {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px 0;
      .label {
        white-space: nowrap;
      }
    }
  }
</style>
